.card {
  width: 800px;
  display: inline-block;
  margin: 2px;
}

.card_header {
  background: #282b2d;
}

.card_header_text {
  float: left;
  color: white !important;
  opacity: 0.60 !important;
}

.card_content {
  background: #f5f5f6;
}

.select_field {
    background: white;
    width: 300px;
    margin-top: 16px;
    margin-bottom: 16px;
}

.typography {
    padding-top: 20px;
    color: #282b2d !important;
}

.typography_error {
  padding-top: 20px;
  color: red !important;
}

.ethereum_symbol {
    float: center;
}

.token_info_header {
    border-style: none;
    border-color: lightgray;
    border-radius: 2.5em;
    height: 68px;
    padding: 8px;
}
